import { AxiosError } from 'axios'
import { useSnackbar } from 'notistack'
import { useEffect, useRef, useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useNavigate, useParams } from 'react-router'
import { useRecoilValue } from 'recoil'

import ControlPointRoundedIcon from '@mui/icons-material/ControlPointRounded'
import HighlightOffRoundedIcon from '@mui/icons-material/HighlightOffRounded'
import type { ButtonProps } from '@mui/material'
import {
  Box,
  Button,
  Card,
  Container,
  IconButton,
  Stack,
  TextField,
  type TextFieldProps,
  Typography,
  useTheme
} from '@mui/material'

import { FIRST_SEMESTER, FIRST_SEMESTER_NAME, SECOND_SEMESTER_NAME } from 'src/data/semester'

import { getClubRecruitInformation } from 'src/api/club'
import { getApplyForm, putApplyForm } from 'src/api/clubManage'

import type { ApplyQuestion } from 'src/models/Form'
import type { Recruit } from 'src/models/Recruit'

import DateForm from 'src/components/DateForm'
import ErrorMessageBox from 'src/components/ErrorMessageBox'
import PageTitleWrapper from 'src/components/PageTitleWrapper'

import PageHeader from 'src/content/manage/PageHeader'

import useSafeAuthUser from 'src/hooks/useSafeAuthUser'
import { dateState } from 'src/recoil/atom'

export interface Question {
  id: number
  content: ApplyQuestion
}

const QUESTION_COUNT = {
  MIN: 1,
  MAX: 7
}

const Form: React.FC = () => {
  const date = useRecoilValue(dateState)
  const { id } = useParams()
  const clubId = Number(id)

  const navigate = useNavigate()
  const authHeader = useAuthHeader()
  const { userInfo } = useSafeAuthUser()
  const { enqueueSnackbar } = useSnackbar()

  const nextQuestionIdRef = useRef(1)

  const [questions, setQuestions] = useState<Array<Question>>([{ id: 0, content: '' }])
  const [recruitTimeInformation, setRecruitTimeInformation] = useState<Partial<Pick<Recruit, 'startAt' | 'endAt'>>>({})

  const club = userInfo?.clubs.find(club => club.id === clubId)

  const initializeStates = async () => {
    try {
      const { startAt, endAt } = await getClubRecruitInformation({ clubId })
      setRecruitTimeInformation({ startAt, endAt })
    } catch (err) {
      if (!(err instanceof AxiosError)) {
        enqueueSnackbar('알 수 없는 에러가 발생했습니다. 다시 시도해주세요.', { variant: 'error' })
        return
      }

      // 모집 정보가 등록되지 않았을 경우 400 코드가 옴 - 먼저 모집 글을 등록하도록 이동시킴
      if (err.response?.status === 400) {
        enqueueSnackbar('먼저 동아리 소개 및 모집 글을 등록해야 합니다.', { variant: 'info' })
        navigate('../intro')
        return
      }

      enqueueSnackbar('서버 오류가 발생했습니다. 다시 시도해주세요.', { variant: 'error' })
      return
    }

    try {
      // 모집 글이 등록된 경우 실행됨 - questions가 항상 빈 배열이거나 string array - 항상 수정모드
      const { questions } = await getApplyForm({ clubId, token: authHeader(), date })
      if (questions.every(question => question.length === 0)) return

      setQuestions(questions.map((question, index) => ({ content: question, id: index })))
      nextQuestionIdRef.current = questions.length
    } catch (err) {
      if (err instanceof AxiosError) {
        if (err.code === 'ERR_BAD_REQUEST') {
          setQuestions([])
          return
        }
        enqueueSnackbar('서버 오류가 발생했습니다. 다시 시도해주세요.', { variant: 'error' })
        return
      }

      enqueueSnackbar('알 수 없는 에러가 발생했습니다. 다시 시도해주세요.', { variant: 'error' })
    }
  }

  useEffect(() => {
    initializeStates()
  }, [date])

  const handleQuestionFieldChange: React.ChangeEventHandler<HTMLTextAreaElement> = ({ target: { name, value } }) => {
    const questionId = Number(name)
    setQuestions(prev => {
      const currentIndex = prev.findIndex(({ id }) => id === questionId)
      const next = [...prev]
      next[currentIndex].content = value
      return next
    })
  }

  const handleAddQuestionButtonClick: React.MouseEventHandler<HTMLButtonElement> = () => {
    if (questions.length >= QUESTION_COUNT.MAX) return
    setQuestions(prev => [...prev, { id: nextQuestionIdRef.current, content: '' }])
    nextQuestionIdRef.current += 1
  }

  const handleDeleteQuestionButtonClick: (questionId: Question['id']) => React.MouseEventHandler<HTMLButtonElement> =
    questionId => () => {
      if (questions.length <= QUESTION_COUNT.MIN) {
        enqueueSnackbar(`질문은 ${QUESTION_COUNT.MIN}개 이상이어야 합니다.`, { variant: 'info' })
        return
      }
      setQuestions(prev => prev.filter(({ id }) => id !== questionId))
    }

  const handleSubmit: React.FormEventHandler<HTMLFormElement> = async e => {
    e.preventDefault()

    const { startAt, endAt } = recruitTimeInformation
    if (!startAt || !endAt) {
      enqueueSnackbar('잘못된 접근입니다.', { variant: 'error' })
      navigate('/')
      return
    }

    try {
      const questionsWithContent = questions
        .map(({ content }) => content.trim())
        .filter(question => question.length > 0)

      await putApplyForm({
        clubId,
        token: authHeader(),
        date,
        questions: questionsWithContent,
        startAt,
        endAt
      })
      enqueueSnackbar('질문을 수정하였습니다.', { variant: 'success' })
    } catch {
      enqueueSnackbar(`질문 수정에 실패했습니다. 다시 시도하여 주십시오.`, {
        variant: 'error'
      })
    }
  }

  const handleCancelButtonClick: React.MouseEventHandler<HTMLButtonElement> = () => {
    navigate(-1)
  }

  return (
    <>
      <Helmet>
        <title>신청폼 관리</title>
      </Helmet>
      <PageTitleWrapper>
        <PageHeader clubName={club?.name ?? ''} link={`/manage/${clubId}/attendance/register`} />
      </PageTitleWrapper>
      <Container maxWidth="lg">
        <DateForm club={club} />
        {questions.length ? (
          <Box component="form" onSubmit={handleSubmit}>
            <Card>
              <Box p={{ xs: 2, md: 5 }}>
                <Typography variant="h2">신청폼 관리</Typography>
                <Typography variant="subtitle1">
                  {`${QUESTION_COUNT.MIN}개부터 ${QUESTION_COUNT.MAX}개까지 작성할 수 있습니다.`}
                </Typography>
                <Box mt={3} sx={{ display: 'flex', flexDirection: 'column' }}>
                  {questions.map(({ content, id }, index) => (
                    <Box position="relative">
                      <DeleteQuestionButton onClick={handleDeleteQuestionButtonClick(id)} />
                      <QuestionTextField
                        key={id}
                        name={`${id}`}
                        label={`질문${index + 1}`}
                        placeholder={`${index + 1}번째 질문을 적어주세요.`}
                        question={content}
                        letterCount={content.length}
                        onChange={handleQuestionFieldChange}
                      />
                    </Box>
                  ))}
                  <AddQuestionButton
                    onClick={handleAddQuestionButtonClick}
                    disabled={questions.length >= QUESTION_COUNT.MAX}
                  />
                </Box>
              </Box>
            </Card>
            <Stack mt={2} pb={2} direction="row" spacing={2} justifyContent="center">
              <Button
                type="button"
                variant="outlined"
                onClick={handleCancelButtonClick}
                sx={{ width: '50%', maxWidth: '120px' }}
              >
                뒤로가기
              </Button>
              <Button type="submit" variant="contained" sx={{ width: '50%', maxWidth: '120px' }}>
                질문 수정
              </Button>
            </Stack>
          </Box>
        ) : (
          <ErrorMessageBox
            children={`${date.year}년도 ${
              date.semester === FIRST_SEMESTER ? FIRST_SEMESTER_NAME : SECOND_SEMESTER_NAME
            }에 작성한 신청폼이 없습니다`}
          />
        )}
      </Container>
    </>
  )
}

export default Form

type QuestionTextFieldProps = { question: TextFieldProps['value']; letterCount: number } & Pick<
  TextFieldProps,
  'onChange' | 'name' | 'label' | 'placeholder'
>

const CHARACTER_LIMIT_COUNT = 500

const QuestionTextField: React.FC<QuestionTextFieldProps> = ({ question, letterCount, ...props }) => {
  const { spacing } = useTheme()

  return (
    <TextField
      variant="standard"
      value={question}
      helperText={`${letterCount} / ${CHARACTER_LIMIT_COUNT}`}
      {...props}
      inputProps={{
        maxLength: CHARACTER_LIMIT_COUNT
      }}
      multiline
      fullWidth
      maxRows={4}
      minRows={3}
      required
      sx={{
        margin: spacing(1, 0),
        padding: spacing(1, 0),
        '& label': { fontSize: '1.25rem' },
        '& .MuiFormHelperText-root': { textAlign: 'right' }
      }}
    />
  )
}

type AddQuestionButtonProps = Pick<ButtonProps, 'onClick' | 'disabled'>

const AddQuestionButton: React.FC<AddQuestionButtonProps> = ({ onClick, disabled }) => {
  return (
    <IconButton
      type="button"
      aria-label="질문 추가하기"
      color="primary"
      onClick={onClick}
      disabled={disabled}
      sx={{ margin: '0 auto', justifySelf: 'center' }}
    >
      <ControlPointRoundedIcon fontSize="small" />
      <Typography variant="button">&nbsp;질문 추가하기</Typography>
    </IconButton>
  )
}

type DeleteQuestionButtonProps = Pick<ButtonProps, 'onClick'>

const DeleteQuestionButton: React.FC<DeleteQuestionButtonProps> = ({ onClick }) => {
  const { spacing } = useTheme()

  return (
    <IconButton
      type="button"
      aria-label="질문 삭제하기"
      color="error"
      onClick={onClick}
      sx={{ position: 'absolute', right: spacing(1), top: spacing(1), zIndex: 5 }}
    >
      <HighlightOffRoundedIcon fontSize="small" />
      <Typography variant="button">&nbsp;삭제</Typography>
    </IconButton>
  )
}
